"use client";

import { ArrowDown, ArrowUp, Minus, Star, Activity, Zap } from "lucide-react";
import { cn } from "@/lib/utils";
import { useState, useMemo } from "react";
import { useRouter } from "next/navigation";
import AutoFilter, { FilterState } from "./AutoFilter";
import { applyColumnFilters } from "@/utils/globalFilter";
import { useToggleWatchlist, useWatchlist, StockStrength } from "@/hooks/useStocks";
import { getUniqueValues, getPctColor } from "@/utils/tableUtils";
import { StrengthMeter } from "@/components/ui/StrengthMeter";

interface StrengthTableProps {
    stocks: StockStrength[];
    isLoading: boolean;
    marketHeaders?: {
        current: string;
        p1: string;
        p2: string;
        p3: string;
    };
}

export default function StrengthTable({ stocks, isLoading, marketHeaders }: StrengthTableProps) {
    const router = useRouter();
    const [displayCount, setDisplayCount] = useState(50);

    // Default headers if not provided
    const headers = {
        current: marketHeaders?.current || "Today Change",
        p1: marketHeaders?.p1 || "Past Day 1 %",
        p2: marketHeaders?.p2 || "Past Day 2 %",
        p3: marketHeaders?.p3 || "Past Day 3 %"
    };

    // Client-side state
    const [columnFilters, setColumnFilters] = useState<Record<string, FilterState>>({});
    const [sortConfig, setSortConfig] = useState<{ key: string; direction: "asc" | "desc" }>({
        key: "current_change",
        direction: "desc"
    });

    const { data: watchlist } = useWatchlist();
    const { mutate: toggleWatchlist } = useToggleWatchlist();

    const isInWatchlist = (symbol: string) => {
        return watchlist?.some((s: any) => s.symbol === symbol);
    };

    const handleToggleWatchlist = (symbol: string) => {
        const action = isInWatchlist(symbol) ? "remove" : "add";
        toggleWatchlist({ symbol, action });
    };

    const handleRowClick = (symbol: string) => {
        router.push(`/stocks/${symbol}`);
    };

    const handleFilterChange = (key: string, filter: FilterState | null) => {
        setColumnFilters(prev => {
            const next = { ...prev };
            if (filter === null) {
                delete next[key];
            } else {
                next[key] = filter;
            }
            return next;
        });
    };

    const handleSort = (key: string, direction: "asc" | "desc") => {
        setSortConfig({ key, direction });
    };

    // Derived Data & Filtering
    const processedStocks = useMemo(() => {
        if (!stocks) return [];

        // 2. Apply Column Filters
        const filtered = applyColumnFilters(stocks, columnFilters);

        // 3. Sort
        return filtered.sort((a, b) => {
            const { key, direction } = sortConfig;
            let valA: any, valB: any;

            // Special key handling for StrengthTable
            // (We might need more robust accessing here if keys overlap with StocksTable)
            if (key === "current_change") valA = a.current_change;
            else if (key === "history.p_day1") valA = a.history?.p_day1;
            else if (key === "day1_strength") valA = a.day1_strength;
            else if (key === "history.p_day2") valA = a.history?.p_day2;
            else if (key === "day2_strength") valA = a.day2_strength;
            else if (key === "history.p_day3") valA = a.history?.p_day3;
            else if (key === "day3_strength") valA = a.day3_strength;
            else if (key === "derived.macdLabel") valA = a.derived?.macdLabel;
            else if (key === "derived.rsiLabel") valA = a.derived?.rsiLabel;
            else {
                valA = key.split('.').reduce((o: any, i: any) => o?.[i], a);
            }

            if (valB === undefined) {
                if (key === "current_change") valB = b.current_change;
                else if (key === "history.p_day1") valB = b.history?.p_day1;
                else if (key === "day1_strength") valB = b.day1_strength;
                else if (key === "history.p_day2") valB = b.history?.p_day2;
                else if (key === "day2_strength") valB = b.day2_strength;
                else if (key === "history.p_day3") valB = b.history?.p_day3;
                else if (key === "day3_strength") valB = b.day3_strength;
                else if (key === "derived.macdLabel") valB = b.derived?.macdLabel;
                else if (key === "derived.rsiLabel") valB = b.derived?.rsiLabel;
                else {
                    valB = key.split('.').reduce((o: any, i: any) => o?.[i], b);
                }
            }

            if (valA === undefined || valA === null) return 1;
            if (valB === undefined || valB === null) return -1;

            if (valA < valB) return direction === "asc" ? -1 : 1;
            if (valA > valB) return direction === "asc" ? 1 : -1;
            return 0;
        });

    }, [stocks, columnFilters, sortConfig]);

    const displayedStocks = processedStocks.slice(0, displayCount);

    const getOptions = (key: string) => {
        if (!stocks.length) return [];
        return getUniqueValues(stocks, key);
    };

    const handleLoadMore = () => {
        setDisplayCount(prev => prev + 50);
    };

    if (isLoading) {
        return (
            <div className="space-y-4">
                {[...Array(5)].map((_, i) => (
                    <div key={i} className="h-16 bg-gray-100 dark:bg-slate-800 rounded-lg animate-pulse" />
                ))}
            </div>
        );
    }

    return (
        <>
            <div className={cn("w-full overflow-x-auto min-h-[600px]",
                "rounded-2xl border border-gray-200 dark:border-slate-700 bg-white dark:bg-slate-800 shadow-sm dark:shadow-md"
            )}>
                <table className="min-w-full table-auto border-separate border-spacing-y-1 text-sm text-left relative">
                    <thead className="text-xs text-gray-600 dark:text-gray-300 uppercase bg-gray-50 dark:bg-slate-800 sticky top-0 z-20 backdrop-blur-sm">
                        <tr>
                            {/* 1. Symbol (w-28) - Sticky Left */}
                            <th className="px-3 py-2 w-28 sticky left-0 z-30 bg-gray-50 dark:bg-slate-800 border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-between gap-2">
                                    <span>Symbol</span>
                                    <AutoFilter
                                        columnKey="symbol"
                                        title="Symbol"
                                        type="text"
                                        options={getOptions("symbol")}
                                        onFilter={(f) => handleFilterChange("symbol", f)}
                                        currentFilter={columnFilters["symbol"] || null}
                                        onSort={(d) => handleSort("symbol", d)}
                                        sortDirection={sortConfig.key === "symbol" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>{headers.current}</span>
                                    <AutoFilter
                                        columnKey="current_change"
                                        title={headers.current}
                                        type="number"
                                        onFilter={(f) => handleFilterChange("current_change", f)}
                                        currentFilter={columnFilters["current_change"] || null}
                                        onSort={(d) => handleSort("current_change", d)}
                                        sortDirection={sortConfig.key === "current_change" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-36 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>Today Str</span>
                                    <AutoFilter
                                        columnKey="current_strength"
                                        title="Today Str"
                                        type="enum"
                                        options={["BUYERS", "SELLERS", "BALANCED"]}
                                        onFilter={(f) => handleFilterChange("current_strength", f)}
                                        currentFilter={columnFilters["current_strength"] || null}
                                        onSort={(d) => handleSort("current_strength", d)}
                                        sortDirection={sortConfig.key === "current_strength" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>{headers.p1}</span>
                                    <AutoFilter
                                        columnKey="history.p_day1"
                                        title={headers.p1}
                                        type="number"
                                        onFilter={(f) => handleFilterChange("history.p_day1", f)}
                                        currentFilter={columnFilters["history.p_day1"] || null}
                                        onSort={(d) => handleSort("history.p_day1", d)}
                                        sortDirection={sortConfig.key === "history.p_day1" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-36 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>P1 Str</span>
                                    <AutoFilter
                                        columnKey="day1_strength"
                                        title="P1 Str"
                                        type="enum"
                                        options={["BUYERS", "SELLERS", "BALANCED"]}
                                        onFilter={(f) => handleFilterChange("day1_strength", f)}
                                        currentFilter={columnFilters["day1_strength"] || null}
                                        onSort={(d) => handleSort("day1_strength", d)}
                                        sortDirection={sortConfig.key === "day1_strength" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>{headers.p2}</span>
                                    <AutoFilter
                                        columnKey="history.p_day2"
                                        title={headers.p2}
                                        type="number"
                                        onFilter={(f) => handleFilterChange("history.p_day2", f)}
                                        currentFilter={columnFilters["history.p_day2"] || null}
                                        onSort={(d) => handleSort("history.p_day2", d)}
                                        sortDirection={sortConfig.key === "history.p_day2" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-36 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>P2 Str</span>
                                    <AutoFilter
                                        columnKey="day2_strength"
                                        title="P2 Str"
                                        type="enum"
                                        options={["BUYERS", "SELLERS", "BALANCED"]}
                                        onFilter={(f) => handleFilterChange("day2_strength", f)}
                                        currentFilter={columnFilters["day2_strength"] || null}
                                        onSort={(d) => handleSort("day2_strength", d)}
                                        sortDirection={sortConfig.key === "day2_strength" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>{headers.p3}</span>
                                    <AutoFilter
                                        columnKey="history.p_day3"
                                        title={headers.p3}
                                        type="number"
                                        onFilter={(f) => handleFilterChange("history.p_day3", f)}
                                        currentFilter={columnFilters["history.p_day3"] || null}
                                        onSort={(d) => handleSort("history.p_day3", d)}
                                        sortDirection={sortConfig.key === "history.p_day3" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-36 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>P3 Str</span>
                                    <AutoFilter
                                        columnKey="day3_strength"
                                        title="P3 Str"
                                        type="enum"
                                        options={["BUYERS", "SELLERS", "BALANCED"]}
                                        onFilter={(f) => handleFilterChange("day3_strength", f)}
                                        currentFilter={columnFilters["day3_strength"] || null}
                                        onSort={(d) => handleSort("day3_strength", d)}
                                        sortDirection={sortConfig.key === "day3_strength" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>3D Avg %</span>
                                    <AutoFilter
                                        columnKey="history.avg_3day"
                                        title="3D Avg %"
                                        type="number"
                                        onFilter={(f) => handleFilterChange("history.avg_3day", f)}
                                        currentFilter={columnFilters["history.avg_3day"] || null}
                                        onSort={(d) => handleSort("history.avg_3day", d)}
                                        sortDirection={sortConfig.key === "history.avg_3day" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 w-40 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>3D Avg Str</span>
                                    <AutoFilter
                                        columnKey="avg_3day_strength"
                                        title="3D Avg Str"
                                        type="enum"
                                        options={["BUYERS", "SELLERS", "BALANCED"]}
                                        onFilter={(f) => handleFilterChange("avg_3day_strength", f)}
                                        currentFilter={columnFilters["avg_3day_strength"] || null}
                                        onSort={(d) => handleSort("avg_3day_strength", d)}
                                        sortDirection={sortConfig.key === "avg_3day_strength" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>

                            {/* 12. Indicators (w-44) */}
                            <th className="px-3 py-2 w-44 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>Indicators</span>
                                    <AutoFilter
                                        columnKey="derived.indicatorsList"
                                        title="Indicators"
                                        type="enum"
                                        options={getOptions("derived.indicatorsList")}
                                        onFilter={(f) => handleFilterChange("derived.indicatorsList", f)}
                                        currentFilter={columnFilters["derived.indicatorsList"] || null}
                                        onSort={(d) => handleSort("derived.indicatorsList", d)}
                                        sortDirection={sortConfig.key === "derived.indicatorsList" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>

                            {/* 13. M (w-16) */}
                            <th className="px-3 py-2 w-16 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>M</span>
                                    <AutoFilter
                                        columnKey="derived.macdLabel"
                                        title="M"
                                        type="enum"
                                        options={["Above", "Below", "Neutral"]}
                                        onFilter={(f) => handleFilterChange("derived.macdLabel", f)}
                                        currentFilter={columnFilters["derived.macdLabel"] || null}
                                        onSort={(d) => handleSort("derived.macdLabel", d)}
                                        sortDirection={sortConfig.key === "derived.macdLabel" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>

                            {/* 14. R (w-16) */}
                            <th className="px-3 py-2 w-16 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>R</span>
                                    <AutoFilter
                                        columnKey="derived.rsiLabel"
                                        title="R"
                                        type="enum"
                                        options={["Over", "Under", "Neutral"]}
                                        onFilter={(f) => handleFilterChange("derived.rsiLabel", f)}
                                        currentFilter={columnFilters["derived.rsiLabel"] || null}
                                        onSort={(d) => handleSort("derived.rsiLabel", d)}
                                        sortDirection={sortConfig.key === "derived.rsiLabel" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>

                            {/* 15. Action (w-12) */}
                            <th className="px-3 py-2 w-12 text-center border-b border-gray-200 dark:border-slate-700">
                                Action
                            </th>
                        </tr >
                    </thead >
                    <tbody className="divide-y divide-gray-200 dark:divide-slate-700">
                        {displayedStocks.map((stock) => (
                            <tr
                                key={stock.symbol}
                                className="hover:bg-gray-100 dark:hover:bg-slate-700/50 transition-colors group cursor-pointer border-b border-gray-200 dark:border-slate-700 last:border-0"
                                onClick={() => handleRowClick(stock.symbol)}
                            >
                                {/* 1. Symbol */}
                                <td className="px-3 py-2 whitespace-nowrap sticky left-0 z-10 bg-white dark:bg-slate-800 group-hover:bg-gray-100 dark:group-hover:bg-slate-700/50 border-r border-gray-200 dark:border-slate-700">
                                    <div className="font-bold text-gray-900 dark:text-gray-100">{stock.symbol}</div>
                                </td>

                                {/* 2. Change % */}
                                <td className={cn("px-3 py-2 whitespace-nowrap font-medium text-center", getPctColor(stock.current_change))}>
                                    {stock.current_change?.toFixed(2) || "0.00"}%
                                </td>

                                {/* 3. Strength */}
                                <td className="px-3 py-2 whitespace-nowrap text-center">
                                    <StrengthMeter
                                        label={stock.current_strength}
                                        buyerScore={stock.indicators.buyer_strength_score}
                                        sellerScore={stock.indicators.seller_strength_score}
                                    />
                                </td>

                                {/* 4. Day 1 % */}
                                <td className={cn("px-3 py-2 whitespace-nowrap text-xs text-center", getPctColor(stock.history.p_day1))}>
                                    {stock.history.p_day1?.toFixed(2) || "-"}%
                                </td>

                                {/* 5. D1 Str */}
                                <td className="px-3 py-2 whitespace-nowrap text-center">
                                    <StrengthMeter label={stock.day1_strength} />
                                </td>

                                {/* 6. Day 2 % */}
                                <td className={cn("px-3 py-2 whitespace-nowrap text-xs text-center", getPctColor(stock.history.p_day2))}>
                                    {stock.history.p_day2?.toFixed(2) || "-"}%
                                </td>

                                {/* 7. D2 Str */}
                                <td className="px-3 py-2 whitespace-nowrap text-center">
                                    <StrengthMeter label={stock.day2_strength} />
                                </td>

                                {/* 8. Day 3 % */}
                                <td className={cn("px-3 py-2 whitespace-nowrap text-xs text-center", getPctColor(stock.history.p_day3))}>
                                    {stock.history.p_day3?.toFixed(2) || "-"}%
                                </td>

                                {/* 9. D3 Str */}
                                <td className="px-3 py-2 whitespace-nowrap text-center">
                                    <StrengthMeter label={stock.day3_strength} />
                                </td>

                                {/* 10. 3D Avg % */}
                                <td className={cn("px-3 py-2 whitespace-nowrap font-medium text-center", getPctColor(stock.history.avg_3day))}>
                                    {stock.history.avg_3day?.toFixed(2) || "0.00"}%
                                </td>

                                {/* 11. 3D Avg Str */}
                                <td className="px-3 py-2 whitespace-nowrap text-center">
                                    <StrengthMeter label={stock.avg_3day_strength} />
                                </td>

                                {/* 12. Indicators */}
                                <td className="px-3 py-2 whitespace-nowrap text-[10px] text-center text-gray-500 dark:text-gray-400">
                                    <div className="flex flex-col items-center gap-0.5">
                                        <span className={cn(stock.derived.macdLabel === "Above" ? "text-green-600 dark:text-green-400" : stock.derived.macdLabel === "Below" ? "text-red-600 dark:text-red-400" : "text-gray-500 dark:text-gray-400")}>
                                            MACD: {stock.derived.macdLabel}
                                        </span>
                                        <span className={cn(stock.derived.rsiLabel === "Under" ? "text-green-600 dark:text-green-400" : stock.derived.rsiLabel === "Over" ? "text-red-600 dark:text-red-400" : "text-gray-500 dark:text-gray-400")}>
                                            RSI: {stock.derived.rsiLabel}
                                        </span>
                                    </div>
                                </td>

                                {/* 13. M */}
                                <td className="px-3 py-2 whitespace-nowrap text-center">
                                    <Activity className={cn("w-4 h-4 mx-auto", stock.derived.macdLabel === "Above" ? "text-green-500 dark:text-green-400" : stock.derived.macdLabel === "Below" ? "text-red-500 dark:text-red-400" : "text-gray-500 dark:text-gray-500")} />
                                </td>

                                {/* 14. R */}
                                <td className="px-3 py-2 whitespace-nowrap text-center">
                                    <Zap className={cn("w-4 h-4 mx-auto", stock.derived.rsiLabel === "Under" ? "text-green-500 dark:text-green-400" : stock.derived.rsiLabel === "Over" ? "text-red-500 dark:text-red-400" : "text-blue-400 dark:text-blue-500")} />
                                </td>

                                {/* 15. Action */}
                                <td className="px-3 py-2 whitespace-nowrap text-center">
                                    <button
                                        className="p-1.5 hover:bg-gray-200 dark:hover:bg-slate-600 rounded-full transition-colors text-gray-400 dark:text-gray-500 hover:text-yellow-500 dark:hover:text-yellow-400"
                                        onClick={(e) => {
                                            e.stopPropagation();
                                            handleToggleWatchlist(stock.symbol);
                                        }}
                                    >
                                        <Star className={cn("w-4 h-4", isInWatchlist(stock.symbol) ? "fill-yellow-500 text-yellow-500 dark:text-yellow-400 dark:fill-yellow-400" : "")} />
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table >
            </div >

            {
                processedStocks.length > displayCount && (
                    <div className="mt-4 flex justify-center">
                        <button
                            onClick={handleLoadMore}
                            className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-full text-sm font-medium transition-colors shadow-sm"
                        >
                            Load More
                        </button>
                    </div>
                )
            }
        </>
    );
}
